




@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');
 
*{
  margin:0;
  padding:0;
} 
/* html{
  scroll-behavior: smooth;
} */

body {
  
  min-height: 100vh;
  max-width: 100vw;
  background-image: linear-gradient(to right,rgb(15, 31, 19),rgb(77, 99, 88));

  /* background: url(/src/myPicFoods/desserts/monika-grabkowska-P1aohbiT-EY-unsplash.jpg); */
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  font-family: 'Lato', Tahoma, Geneva, Verdana, sans-serif;
  color:#a5a2a2;
  position: relative;
}


hr {
  /* margin-top: 60px; */
  border: none;
  width: 80%;
  margin:20px auto;
  border-top: 3px double  #778885;;
  color:  #778885;;
  overflow: visible;
  text-align: center;
  height: 10px;
}

hr::after {
  background: #fff;
  content: '§§';
  padding: 0 4px;
  position: relative;
  top: -13px;
} 


.myImoji{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-flow:column;
}

